@import url(https://fonts.googleapis.com/css2?family=Neucha&display=swap);
body {
  /* font-family: 'Inter' !important; */
  font-family: 'Neucha' !important;
  background: #bbb;
  padding: 20px;
}

input:focus, button:focus {
  box-shadow: none !important;
  outline: none !important;
}

.col {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  padding: 30px 40px;
  background: #eee;
  margin: 50px auto;
  max-width: 400px;
  /* border: 1px solid #333; */
}

h1 {
  font-weight: bold;
}

form {
  margin: 20px 0;
}

.form-control {
  width: 40%;
  margin-right: 20px;
  border: 1px solid #333;
  border-radius: 0;
}

.form-control:focus {
  border-color: #333 !important;
}

.add-btn  {
  width: 20%;
  margin-left: 20px;
  background: #fff;
  border: 1px solid #333;
  border-radius: 0;
  color: #000;
}

.add-btn :hover, .add-btn :focus {
  color: #666;
}

.list-group-item  {
  margin: 10px 0;
  border: 1px solid #333;
  border-top: 1px solid #333 !important;
  font-weight: bold;
  color: #000; 
  padding-left: 15px;
  position: relative;
  z-index: 996;
}

.list-group-item.hide {
  display: none;
}

.list-group-item:hover svg {
  color: #000;
}

.list-group-item:focus {
  color: #000;
}

svg {
  font-size: 22px;
  position: absolute;
  right: 10px;
  z-index: 999;
}

svg:hover {
  transform: scale(0.92);
}

.clear-btn {
  background: #fff;
  border: 1px solid #333;
  border-radius: 0;
  color: #000;
}

select {
  box-shadow: none !important;
  border: 1px solid #333 !important;
  border-radius: 0 !important;
  cursor: pointer;
}

option {
  cursor: pointer;
}
